import { isObject, isString, isBool, isArray } from '../utils/is.js';

export function decodeSelector( selector ){
	var clIndex, idIndex;

	const __core = {

		getStop: function( str ){

			const index = str.search( /[^a-z0-9\-_.]/gi );

			return index > -1 ? index : str.length;

		},

		get: function( index, isId ){
			const str = selector.slice( index + 1 );
			const end = __core.getStop( str );
			const array = ( str.slice( 0, end ) ).split( '.' );
			const classes = [];
			var id = null;
			var a;

			isId = isBool( isId ) && isId;

			if( !isArray( array ) || array.length < 1 ){
				return false;

			}

			for( a = 0; a < array.length; a++ ){

				if( array[a].length < 1 ){
					continue;

				}

				if( a === 0 && isId ){
					id = array[a];
					continue;
				}
				classes[classes.length] = array[a];

			}

			return {
				classList: classes,
				id: id
			};


		}

	};

	if( !isString( selector ) || selector.length < 1 ){
		return false;

	}
	clIndex = selector.indexOf( '.' );
	idIndex = selector.indexOf( '#' );

	if( clIndex < 0 && idIndex < 0 ){

		return {
			tagName: selector,
			classList: [],
			id: null

		};

	}

	if( idIndex > -1 ){

		if( selector.length -1 > idIndex ){
			return __core.get( idIndex, true );

		}

	}

	if( clIndex > -1 ){

		if( selector.length -1 > clIndex ){
			return __core.get( clIndex );

		}

	}
	return false;

}

export function decodeSelectors( selectors ){
	const _selectors = [];
	var s, a, decoded;

	if( !isString( selectors ) || ( s = selectors.split( ' ' ) ).length < 1 ){
		return false;

	}

	for( a = 0; a < s.length; a++ ){

		if( s[a].length < 1 || !( decoded = decodeSelector( s[a] ) ) ){
			continue;

		}
		_selectors[_selectors.length] = decoded;

	}
	return _selectors;

}